<template>
	<div class="setting-sign">
		<input maxlength="20" ref="sign_input" placeholder="说点什么吧" @input="changeFn($event)" @keydown="keydownFn($event)"></input>
		<div class="tips-num">{{inputLength}}/20</div>
		<div class="submit">保存</div>
	</div>
</template>

<script>
export default {
	name: 'SettingSign',
	data () {
		return {
			inputLength:0
		}
	},
	created (){
		document.title="修改签名";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.$refs['sign_input'].focus();
	},
	methods: {
		changeFn:function (e) {
			console.log(e.target.value.length)
			inputLength=e.target.value.length;
		},
		keydownFn:function (e) {
			if(e.keyCode == "13"){
				e.target.blur();
			};
		},
	}
}
</script>

<style scoped>
	.setting-sign{
		text-align: center;
	}
	input{
		width: 96%;
		outline:none;
		padding: 1rem;
		box-sizing: border-box;
		background-color: #F3F3F3;
		box-sizing: border-box;
		margin: 0.5rem 0;
	}
	.tips-num{
		text-align: right;
		padding: 0 1rem;
		color: #999;
	}
	.submit{
		width: 40%;
		font-size: 1.125rem;
		text-align: center;
		margin: 1rem auto;
		background-color: #000;
		color: #fff;
		padding: 0.5rem 0;
		border-radius: 0.25rem;
	}
</style>



