<template>
	<div class="mine-data">
		<input type="file" id="file" class="hide" accept="image/*" @change="changeHeader($event)">
		<ul class="mine-data-list">
			<li class="other-data-title">基本资料</li>
			<li class="mine-data-items" @click="clickHeader">
				<div class="items-title">头像</div>
				<div class="items-value">
					<div class="icon"><img :src="userAvatar"></div>
				</div>
			</li>
			<li class="mine-data-items">
				<div class="items-title">昵称</div>
				<div class="items-value" id="mine-nickname">{{userNickname}}</div>
			</li>
			<router-link tag="li" to="/SettingSign" class="mine-data-items">
				<div class="items-title">签名</div>
				<div class="items-value" id="mine-sign">{{signature}}</div>
			</router-link>
			<li class="mine-data-items">
				<div class="items-title">性别</div>
				<div class="items-value" id="mine-six">
					<select class="mine-six" dir="rtl" @change="selectFn">
						<option value="0" :selected="sex==0?'selected':''" class="hide">未设置</option>
						<option value="1" :selected="sex==1?'selected':''">男</option>
						<option value="2" :selected="sex==2?'selected':''">女</option>
					</select>
				</div>
			</li>
		</ul>
		<ul class="other-data-list">
			<li class="other-data-title">账号安全</li>
			<li class="mine-data-items">
				<div class="items-title">手机号</div>
				<div class="items-value" id="mine-phone">{{telephone}}</div>
			</li>
			<router-link tag="li" to="/MineChangepsw" class="mine-data-items">
				<div class="items-title">修改密码</div>
				<div class="items-value"></div>
			</router-link>
		</ul>
	</div>
</template>

<script>
import userAvatar from '../assets/ico_header.png'
export default {
	name: 'MineData',
	data () {
		return {
			userNickname:"",
			userAvatar:userAvatar,
			telephone:"",
			signature:"未设置",
			sex:0,
		}
	},
	created (){
		document.title="修改个人信息";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {
			CF.showLoading(1);
			axios.get("/api/user/1").then(res=>{
				console.log(res)
				if (res.data.code==1001) {
					CF.showLoading(false);
					var userData = res.data.response.data;
					this.userAvatar=userData.user_avatar!="默认图片"?userData.user_avatar:this.userAvatar;
					this.userNickname=userData.user_nickname;
					this.signature=userData.user_signature!=""?user_signature:this.signature;
					this.telephone=userData.user_telephone;
					this.sex=userData.user_sex;
				}
			})
		},
		clickHeader:function(){
			$("#file").click();
		},
		changeHeader : function(e){
			var URL = window.URL || window.webkitURL;
			var imgURL = URL.createObjectURL(e.currentTarget.files[0]);
			$(".icon").html("");
			$(".icon").css({
				'background': 'url('+imgURL+') center no-repeat',
				'background-size': 'cover',
				'background-clip':'content-box'
			});
		},
		selectFn:function(e){

		}
	}
}
</script>

<style scoped>
	.mine-data{
		padding: 1rem;
	}
	.mine-data-list{
		margin-bottom: 1rem;
	}
	.mine-data-items{
		border-bottom: 1px solid #e5e5e5;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 1rem 1rem 1rem 0;
	}
	.items-value{
		color: #666;
	}
	.items-value select{
		font-size: 1rem;
		color: #666;
		outline: none;
		line-height:1rem;
		appearance:none;
		-webkit-appearance:none;
		border:none;
		background-color:white;
	}
	.items-value .icon{
		width: 1.562rem;
		height: 1.562rem;
		overflow: hidden;
		border-radius: 2rem;
	}
	.items-value .icon img{
		display: block;
	}
	.other-data-title{
		color: #999;
		margin-bottom: 1rem;
	}
</style>



