<template>
	<div class="search-more">
		<div class="search-input">
			<div class="search-left">
				<div class="search-bg">
					<form onsubmit="return false;"><input ref="input" type="search" placeholder="输入关键字" id="searchKey"/></form>
				</div>
			</div>
			<div class="search-right">
				<div class="search-cancel" @click="back">取消</div>
			</div>
		</div>
		<div class="search-result">
			<ul class="search-result-list" v-if="isDesigner">
				<li class="search-result-title">设计师</li>
				<router-link tag="li" class="search-result-value" :to="{ path: '/DesignerDetail/did/1'}">
					<div class="search-result-img"><img src="../assets/ico_header.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">他与柠檬</div>
						<div class="search-result-intro">品牌设计师</div>
					</div>
				</router-link>
				<router-link tag="li" class="search-result-value" :to="{ path: '/DesignerDetail/did/1'}">
					<div class="search-result-img"><img src="../assets/ico_header.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">他与柠檬</div>
						<div class="search-result-intro">品牌设计师</div>
					</div>
				</router-link>
			</ul>
			<ul class="search-result-list" v-if="isWorks">
				<li class="search-result-title">作品</li>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">戏子</div>
						<div class="search-result-intro">灵感来自中国戏剧，灵感来自中国戏剧，灵感来自中国戏剧</div>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">戏子</div>
						<div class="search-result-intro">灵感来自中国戏剧，灵感来自中国戏剧，灵感来自中国戏剧</div>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">戏子</div>
						<div class="search-result-intro">灵感来自中国戏剧，灵感来自中国戏剧，灵感来自中国戏剧</div>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">戏子</div>
						<div class="search-result-intro">灵感来自中国戏剧，灵感来自中国戏剧，灵感来自中国戏剧</div>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text">
						<div class="search-result-name">戏子</div>
						<div class="search-result-intro">灵感来自中国戏剧，灵感来自中国戏剧，灵感来自中国戏剧</div>
					</div>
				</router-link>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
	name: 'search-more',
	data () {
		return {
			isDesigner:false,
			isWorks:false,
		}
	},
	created (){
		if (this.$route.params.type=="designer") {
			this.isDesigner=true;
			document.title="更多设计师"
		}
		if (this.$route.params.type=="works") {
			this.isWorks=true;
			document.title="更多作品"
		}
	},
	mounted:function(){
		this.$refs['input'].focus();
		this.$nextTick(function(){
			// this.matchsPage();
		})
	},
	methods:{
		back:function(){
			history.back();
		}
	},
}
</script>

<style scoped>
	.search-input{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 0.5rem 2rem 0.5rem 0.8rem;
		border-bottom: 1px solid #e5e5e5;
	}
	.search-left{
		width: 80%;
		background-color: #f5f5f5;
		border-radius: 0.312rem;
		padding: 0.3rem 0.5rem;
	}
	.search-bg{
		background: url("../assets/ico-search.png") left no-repeat;
		background-size: auto 1.0rem;
	}
	.search-left input{
		background:transparent;
		border:none;outline:none;
		-webkit-tap-highlight-color:transparent;
		padding-left: 1.5rem;
	}
	.search-cancel{
		font-size: 0.938rem;
	}
	.search-result-list{
		padding: 0.8rem 0.8rem 0;
	}
	.search-result-list li{
		border-bottom: 1px solid #e5e5e5;
	}
	.search-result-title{
		padding-bottom: 0.2rem;
		color: #999;
	}
	.search-result-value{
		width: 100%;
		display: flex;
		align-items: center;
		display: -webkit-flex;
		-webkit-align-items: center;
		padding: 0.5rem 0;
	}
	.search-result-img{
		width: 12%;
		margin-right: 1rem;
		flex-shrink:0;
		-webkit-flex-shrink:0;
	}
	.search-result-text{
		width: 82%;
	}
	.search-result-intro{
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		color: #999;
	}
</style>



