<template>
	<div class="mine">
		<div class="mine-wrap" v-if="isLogin">
			<router-link tag="div" to="MineData" class="mine-data">
				<div class="header">
					<div class="icon">
						<img :src='userAvatar'>
					</div>
					<div class="label" v-if="userLabel">PRO</div>
				</div>
				<div class="nickname">{{userNickname}}</div>
				<div class="personalized-signature">{{signature}}</div>
			</router-link>
			<ul class="mine-list">
				<router-link tag="li" to="UploadWorks" class="mine-items">
					<div class="items-title">上传作品</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="ApplicationDesigner" class="mine-items">
					<div class="items-title">申请成为认证设计师</div>
					<div class="items-value">{{userLabel?"已认证":"未认证"}}</div>
				</router-link>
				<router-link tag="li" to="MineWorks" class="mine-items">
					<div class="items-title">我的作品</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="MineOrder" class="mine-items">
					<div class="items-title">我的订单</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="MineCollect" class="mine-items">
					<div class="items-title">商品收藏</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="MineCoupon" class="mine-items">
					<div class="items-title">优惠券</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="MineIncome" class="mine-items">
					<div class="items-title">收益</div>
					<div class="items-value"></div>
				</router-link>
				<router-link tag="li" to="MineSetting" class="mine-items">
					<div class="items-title">设置</div>
					<div class="items-value"></div>
				</router-link>
			</ul>
		</div>

		<div class="login-wrap" v-if="!isLogin">
			<div class="logo-box">
				<div class="logo">ONE T</div>
				<div class="text">让创意留存</div>
			</div>
			<div class="login-select">
				<router-link tag="div" to="/Login" class="option-login">用户登录</router-link>
				<router-link tag="div" to="/Register/type/register" class="option-register">新用户注册</router-link>
				<router-link tag="div" to="/Register/type/setting" class="forget-psw">忘记密码</router-link>
			</div>
		</div>
	</div>
</template>

<script>
import userAvatar from '../assets/ico_header.png'
export default {
	name: 'Mine',
	data () {
		return {
			isLogin:false,
			userLabel:false,
			userNickname:"",
			userAvatar:userAvatar,
			signature:"此人没有填写个性签名~",
		}
	},
	created (){
		document.title="我的";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init();
	},
	methods: {
		init: function () {
			console.log(_userData_);
			var userData = _userData_;
			if (userData) {
				this.userAvatar=userData.user_avatar!="默认图片"?userData.user_avatar:this.userAvatar;
				this.userLabel=userData.user_label==1?true:false;
				this.userNickname=userData.user_nickname;
				this.signature=userData.user_signature!=""?user_signature:this.signature;

				this.isLogin=true;
			}
		},
	}
}
</script>

<style scoped>
	.select{
		position: fixed;
		left: 0;
		top: 0;
		color: #fff;
		z-index: 999;
		background-color: #000;
		width: 5rem;
		height: 3rem;
	}
	.header{
		width: 8rem;
		height: 8rem;
		position: relative;
		overflow: hidden;
		margin:1.5rem auto;
	}
	.header .icon{
		border-radius: 8rem;
		overflow: hidden;
	}
	.header .label{
		position: absolute;
		right: 1rem;
		bottom: 0;
		background-color: #000;
		color: #fff;
		font-weight: bold;
		font-size: 0.75rem;
		padding: 0.2rem;
	}
	.nickname{
		text-align: center;
		font-size: 1.125rem;
	}
	.personalized-signature{
		color: #666;
		font-size: 0.875rem;
		text-align: center;
		margin: 1rem;
	}
	.mine-list{
		padding: 2rem;
		color: #666;
	}
	.mine-items{
		border-bottom: 1px solid #e5e5e5;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 1rem 1rem 1rem 0;
	}
	.items-value{
		color: #999;
	}
	.login-wrap{
		width: 100%;
		height: 100%;
		background-color: #000;
		color: #fff;
		text-align: center;
		position: absolute;
		left: 0;
		top: 0;
	}
	.logo-box{
		margin: 10rem 0;
	}
	.login-select{
		width: 100%;
		position: absolute;
		left: 0;
		bottom: 2rem;
		padding: 1rem;
		box-sizing: border-box;
	}
	.login-select>div{
		margin: 1rem 0;
	}
	.option-login{
		border:1px solid #fff;
		background-color: #fff;
		color: #000;
		padding: 0.8rem;
	}
	.option-register{
		border:1px solid #fff;
		padding: 0.8rem;
	}
	.login-select .forget-psw{
		font-size: 0.875rem;
		margin-top: 2rem;
	}
</style>



