<template>
	<div class="mine-changepsw">
		<ul class="changepsw-list">
			<li class="changepsw-items">
				<div class="changepsw-items-left">
					<span>旧密码</span>
					<input class="changepsw-input" v-model="inputVal1" ref="old_psw" type="password" maxlength="16" placeholder="请输入旧密码" onkeyup="this.value=this.value.replace(/\s+/g,'')">
				</div>
				<div class="changepsw-items-right" v-show="inputShow1" data-bol="false" @click="togglePswFn($event)">显示</div>
			</li>
			<li class="changepsw-items">
				<div class="changepsw-items-left">
					<span>新密码</span>
					<input class="changepsw-input" v-model="inputVal2" ref="new_psw" type="password" maxlength="16" placeholder="密码为6-16位符号、数字或字母" onkeyup="this.value=this.value.replace(/\s+/g,'')">
				</div>
				<div class="changepsw-items-right" v-show="inputShow2" data-bol="false" @click="togglePswFn($event)">显示</div>
			</li>
		</ul>
		<div class="change-btn" id="change_btn" @click="changeFn">修改</div>
	</div>
</template>

<script>
export default {
	name: 'MineChangepsw',
	data () {
		return {
			inputVal1:"",
			inputVal2:"",
			inputShow1:false,
			inputShow2:false,
		}
	},
	created (){
		document.title="修改密码";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	watch:{
		inputVal1(curVal,oldVal){
			if(curVal.length>0) this.inputShow1=true;
		},
		inputVal2(curVal,oldVal){
			if(curVal.length>0) this.inputShow2=true;
		}
　　},
	methods: {
		changeFn :function(){
			var oPassword = this.inputVal1;
			var nPassword = this.inputVal2;
			axios.post("/api/user/password",{"old_password":oPassword,"password":nPassword}).then(res=>{
				if (res) {
					console.log(res)
					if (res.data.code==1001) {
						CF.showLoading(false);
						CF.showTips("修改成功");
						history.black();
					}else{
						CF.showLoading(false);
						CF.showTips(res.data.msg)
					}
				}else{

				}
			}).catch(function (error) {
				CF.showTips("验证失败")
			});
			setTimeout(function(){
				history.back();
			},1000)
		},
		togglePswFn:function(e){
			if ($(e.target).attr("data-bol")=="false") {
				$(e.target).attr("data-bol","true")
				$(e.target).text("隐藏")
				$(e.target).prev().find('input').attr("type","text");
			}else{
				$(e.target).attr("data-bol","false")
				$(e.target).text("显示")
				$(e.target).prev().find('input').attr("type","password");
			}
		}
	}
}
</script>

<style scoped>
	.mine-changepsw{
		padding: 1rem;
	}
	.changepsw-list{
		margin: 2rem 0;
	}
	.changepsw-items{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;
		display: flex;
		display: -webkit-flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
	}
	.changepsw-items-left{
		width: 88%;
	}
	.changepsw-input{
		width: 75%;
		margin-left: 1rem;
	}
	.change-btn{
		padding: 0.8rem;
		text-align: center;
		color: #fff;
		background-color: #000;
	}
</style>



