<template>
	<div class="after-sales">
		<div class="order-details bottom-space">
			<div class="order-icon"></div>
			<div>
				<p class="order-title">席子</p>
				<p class="order-style">颜色:白色，尺码:L，类型:经典款</p>
			</div>
		</div>
		<div class="cause bottom-space">
			<div class="select-box">
				<div>原因</div>
				<select name="" id="" dir="rtl">
					<option selected="selected" class="hide">请选择</option>
					<option value="1">衣服质量问题</option>
					<option value="2">打印模糊不清</option>
					<option value="3">打印图案不完整</option>
				</select>
			</div>
			<div class="text">
				<p>如非属衣服质量或者打印图案质量问题</p>
				<p>一概不可退货退款</p>
			</div>
		</div>
		<div class="describe">
			<div class="title">问题描述</div>
			<textarea name="" @keyup="textareaFn($event)"></textarea>
			<div class="input-num">({{textNum}}/500)</div>
		</div>
		<div class="file">
			<div class="file-imgs-box"></div>
			<div class="camera" @click="addFile"><img src="../assets/ico_camera.png"></div>
		</div>
		<div class="submit" @click="submitFn">提交</div>
	</div>
</template>

<script>
export default {
	name: 'AfterSales',
	data () {
		return {
			textNum:0,
			fileInputNum:1
		}
	},
	created (){
		document.title="申请售后服务";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		},
		textareaFn:function(e){
			this.textNum = $(e.target).val().length;
			$(e.target).scrollTop(500);
		},
		addFile:function(){
			var _this=this;
			var fileInputNum = this.fileInputNum;
			if ($("#file_input"+fileInputNum).length>0) $("#file_input"+fileInputNum).remove();
			if (fileInputNum>9) return;
			var fileInput = '<input class="hide" id="file_input'+fileInputNum+'" type="file" accept="image/*">'
			$(".after-sales").append(fileInput);
			$(fileInput).click().on("change",function() {
				console.log(fileInputNum)
				_this.fileInputNum++;
				var URL = window.URL || window.webkitURL;
			    var imgURL = URL.createObjectURL(this.files[0]);
				var fileImg='<div class="file-img" id="file_img'+fileInputNum+'"></div>'
				var $elm = $(fileImg)
				$(".file-imgs-box").prepend($elm);
				var elmHeight = $elm.outerWidth();
				$($elm).css({
					'height':elmHeight,
					'background': 'url('+imgURL+') center no-repeat',
					'background-size': 'cover',
					'background-clip':'content-box'
				});
				if (_this.fileInputNum>9) {
					$(".camera").hide();
				}
			});
		},
		submitFn:function(){
			this.$router.push({ path: '/AfterSalesSubmit' })
		}
	}
}
</script>

<style>
	.order-details{
		display: flex;
		display: -webkit-flex;
		padding: 1rem;
		font-size: 0.875rem;
	}
	.order-icon{
		width: 3.5rem;
		height: 3.5rem;
		background: url("../assets/clothes-bg.png") center left no-repeat;
		background-size: 100% auto;
		flex-shrink:0;
		-webkit-flex-shrink:0;
		margin-right: 1rem;
	}
	.cause{
		padding: 0.8rem;
	}
	.select-box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.8rem;
		padding-right: 0.8rem;
	}
	.order-style{
		color: #999;
	}
	.cause .text{
		color: #999;
		font-size: 0.875rem;
		margin-top: 0.5rem;
	}
	.describe{
		padding: 0.8rem;
		position: relative;
	}
	.describe .title{
		color: #666;
	}
	.describe textarea{
		width: 100%;
		height: 7rem;
		background-color: #F3F3F3;
		font-size: 0.75rem;
		resize:none;
		padding: 0.5rem 0.5rem 1.5rem 0.5rem;
		box-sizing: border-box;
		margin-top: 0.8rem;
	}
	.describe .input-num{
		color: #666;
		font-size: 0.75rem;
		position: absolute;
		right: 1.5rem;
		bottom: 1.5rem;
	}
	.file-imgs-box{
		display: flex;
		display: -webkit-flex;
		flex-wrap: wrap;
		-webkit-flex-wrap: wrap;
		padding: 0.3rem;
		box-sizing: border-box;
	}
	.file-img{
		width: 25%;
		padding: 0.5rem;
		box-sizing: border-box;
	}
	.camera{
		width: 2rem;
		padding: 0 0.8rem;
	}
	.submit{
		width: 40%;
		background-color: #000;
		color: #fff;
		margin: auto;
		text-align: center;
		font-size: 0.875rem;
		line-height: 2rem;
		border-radius: 0.25rem;
		margin: 4.5rem auto;
	}
</style>



