<template>
	<div class="complete-pay">
		<div class="good-detail">
			<div class="good-img">
				<img src="../assets/clothes-bg.png">
			</div>
			<div class="goods-label">
				<p class="good-title">休斯顿的瓦解</p>
				<p class="good-size">颜色：白色，尺码：L，类型：经典款</p>
				<p class="good-price">¥89.00</p>
			</div>
		</div>

		<div class="nearby-machine">
			<p class="title">我附近的机器</p>
			<div class="map" id="container" style="height:12.5rem"></div>
			<div class="location">
				<img src="../assets/ico-location.png">
				<span>珠江新城兴盛路111号</span>
			</div>
		</div>

		<ul class="order-detail">
			<li class="order-number">订单编号：123456789987</li>
			<li class="order-creat-time">创建时间：2017-09-11 14:25:20</li>
			<li class="order-pay-time">付款时间：2017-09-11 14:25:20</li>
		</ul>

		<ul class="order-list">
			<li class="order-items">
				<div class="order-title">我的全部订单</div>
				<div class="order-value"></div>
			</li>
		</ul>
	</div>
</template>

<script>
import AMap from 'AMap'
export default {
	name: 'CompletePay',
	data () {
		return {
			msg:"下单"
		}
	},
	created (){
		document.title="购买完成";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {
			var map, geolocation;
			//加载地图，调用浏览器定位服务
			map = new AMap.Map('container', {
				resizeEnable: true,
				zoom: 11,
			});
			map.plugin('AMap.Geolocation', function() {
				geolocation = new AMap.Geolocation({
					enableHighAccuracy: true,//是否使用高精度定位，默认:true
					timeout: 10000,			//超过10秒后停止定位，默认：无穷大
					buttonOffset: new AMap.Pixel(0, 0),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
					buttonPosition:'RB'
				});
				map.addControl(geolocation);
				geolocation.getCurrentPosition();
				AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
				AMap.event.addListener(geolocation, 'error', onError);	//返回定位出错信息
			});
			function onComplete(data) {
				alert('定位成功');
				// str.push('经度：' + data.position.getLng());
				// str.push('纬度：' + data.position.getLat());
			}
			function onError(data) {
				alert('定位失败')
			}
		}
	}
}
</script>

<style scoped>
	.good-detail{
		display: flex;
		display: -webkit-flex;
		padding: 1rem;
	}
	.good-img{
		width: 5rem;
		height: 5rem;
		border: 1px solid #e5e5e5;
		padding: 0.2rem;
		margin-right: 1rem;
	}
	.good-title{
		padding: 0.2rem 0;
	}
	.good-size{
		color:#999;
		font-size: 0.75rem;
	}
	.good-price{
		padding: 1rem 0;
	}
	.nearby-machine .title{
		padding: 1rem;
	}
	.nearby-machine .location{
		font-size: 0.875rem;
		color: #666;
		padding: 0.5rem;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
	}
	.nearby-machine .location img{
		width:0.7rem;
		margin-right: 0.5rem;
	}
	.order-detail{
		padding: 1rem;
		font-size: 0.875rem;
		color: #666;
	}
	.order-list{
		padding: 1.5rem 1rem;
	}
	.order-items{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding-right: 1rem;
		background: url(../assets/ico-right.png) right no-repeat;
		background-size: auto 0.75rem;
	}
	.order-value{
		color: #999;
	}
</style>



