<script>
import Swiper from '../../static/js/swiper.min.js'
let swiper

export default {
	name: 'home',
	data () {
		return {
			classifyList:"",
			productList:"",
			loading:true
		}
	},
	created (){
		document.title="ONET";
	},
	mounted () {
		CF.showLoading(1);
		this.getData();
		this.swiperFn();
	},
	methods: {
		getData : function () {
			axios.get("/api/product_classify").then(res=>{
				console.log(res)
				if (res.data.code==1001) {
					CF.showLoading(false);
					this.classifyList=res.data.response.data;
				}
			})
			axios.get("/api/product").then(res=>{
				console.log(res)
				if (res.data.code==1001) {
					this.loading=false;
					this.productList=res.data.response.data;
				}
			})
		},
		swiperFn :function(){
			swiper = new Swiper('.swiper-container', {
			    pagination: '.swiper-pagination',
			    slidesPerView: 3.3,
			    paginationClickable: true,
			    spaceBetween: 10,
			    freeMode: true,
			    observer:true,
			});
		}
	}
}
</script>

<style scoped>
	.goods-list{display: flex;display: -webkit-flex;flex-wrap: wrap;-webkit-flex-wrap: wrap;justify-content: space-between;-webkit-justify-content: space-between;}
	.goods-items{width: 48%;padding: 1.5rem;font-size: 0.75rem;box-sizing: border-box;flex-shrink:0;-webkit-flex-shrink:0;text-align: center;position: relative;}
	.designer-laber{font-size: 0.75rem;background-color: #000;color: white;padding: 0 0.2rem;position: absolute;top: 0;right: 0;}
	.goods-details{background: url("../assets/clothes-bg.png") top no-repeat;background-size: 100% auto;}
	.goods-logo{width: 42%;margin-top: 1.8rem;}
	.designer-header{width: 18%;margin-top: 0.5rem;}
	.home-title{
		display: flex;
		display: -webkit-flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		align-items: center;
		padding: 0.8rem 0.8rem 0;
	}
	.logo{
