		background-size: auto 1.0rem;
	}
	.subject-tab{
		padding:0.3rem 0.8rem;
	}
	.subject-items img{
		display: block;
	}
	.subject-items-title{
		width: 100%;
		height: 1.2rem;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		margin: auto;
		text-align: center;
		color: #fff;
		font-weight: bold;
		font-size: 1rem;
	}
	.home-goods{
