<template>
	<div class="after-sales-submit">
		<div class="submit-icon">
			<img src="../assets/ico_submit_s.png">
		</div>
		<p class="submit-status">售后服务单申请成功</p>
		<div class="submit-details">
			<p class="submit-time">申请时间：2017-09-07</p>
			<p class="submit-type">申请类型：退货退款</p>
		</div>
		<p class="submit-tips">我们将在48小时内给您回复，期间请耐心等候，如紧急可拨打客服电话</p>
		<div class="call-me">
			<img src="../assets/ico_service.png">
			<span>电话客服</span>
		</div>
	</div>
</template>

<script>
export default {
	name: 'AfterSalesSubmit',
	data () {
		return {
		}
	},
	created (){
		document.title="提交成功";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.submit-icon{
		padding-top: 2.5rem;
		text-align: center;
	}
	.submit-icon img{
		width: 3.5rem;
		margin: 0.5rem;
	}
	.submit-status{
		text-align: center;
		font-size: 0.875rem;
	}
	.submit-details{
		width: 10rem;
		margin: 1rem auto;
	}
	.submit-details p{
		line-height: 2rem;
		font-size: 0.875rem;
	}
	.submit-tips{
		font-size: 0.75rem;
		text-align: center;
		color: #999;
		margin-top: 4rem;
	}
	.call-me{
		width: 40%;
		height: 2rem;
		background-color: #000;
		color: #fff;
		font-size: 0.875rem;
		display: flex;
		align-items: center;
		justify-content: center;
		display: -webkit-flex;
		-webkit-align-items: center;
		-webkit-justify-content: center;
		margin: 2rem auto;
		border-radius: 0.25rem;
	}
	.call-me img{
		width: auto;
		height: 0.875rem;
		margin-right: 0.5rem;
	}
</style>



