<template>
	<div class="withdrawal">
		<div class="add-bank-card hide">
			<div class="bg"><img src="../assets/bg-withdrawal.png"></div>
			<div class="tips">还没添加银行卡</div>
			<div class="add-btn">立刻添加</div>
		</div>
		<div class="withdrawal-box">
			<router-link tag="div" to="BankCard" class="withdrawal-bank">
				<div class="bank-logo"><img src="../assets/ico-bank-js.png"></div>
				<div class="bank-news">
					<p class="bank-name">建设银行(尾号0728储蓄卡)</p>
					<p class="bank-tips">提现手续费率0.01%</p>
				</div>
			</router-link>
			<div class="withdrawal-amount">
				<p class="withdrawal-amount-title">提现金额</p>
				<div class="withdrawal-amount-input">
					<span>￥</span>
					<input v-model="inputVal" id="amount_input" type="number">
				</div>
				<div class="withdrawal-amount-tips">
					<span v-show="inputVal==''">可提现余额{{amount|formatMoney}}，</span>
					<span v-show="inputVal==''" class="withdrawal-all" @click="inputVal=amount">全部提现</span>
					<span v-show="inputVal>=50">额外扣除￥{{inputVal*0.01}}手续费</span>
					<span v-show="inputVal<50&&inputVal!=''">提现金额需大于或等于50元</span>
				</div>
			</div>
			<div class="withdrawal-btn" id="withdrawal_btn" :class="inputVal>=50?'click':''" @click="withdrawalFn">提现</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Withdrawal',
	filters:{
		formatMoney: function(val){
			return "￥" + val.toFixed(2);
		}
	},
	data () {
		return {
			amount:58,
			inputVal:"",
		}
	},
	created (){
		document.title="提现";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	watch:{
		inputVal(curVal,oldVal){
			if(curVal>this.amount) this.inputVal=this.amount;
		}
　　},
	methods: {
		withdrawalFn:function(){
			if(this.inputVal>=50) this.$router.push({ path: '/WithdrawalDetails' })
		}
	}
}
</script>

<style scoped>
	.withdrawal{
		font-size: 0.875rem;
	}
	.add-bank-card .bg{
		margin-top: 4.5rem;
		text-align: center;
	}
	.add-bank-card .bg img{
		width: 25%;
	}
	.add-bank-card .tips{
		text-align: center;
		padding: 2rem;
		color: #666;
	}
	.add-btn{
		width: 80%;
		margin: auto;
		background-color: #000;
		color: #fff;
		line-height: 2.5rem;
		border-radius: 0.25rem;
	}
	.withdrawal-box{
		padding: 1rem;
	}
	.withdrawal-bank{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		background: url(../assets/ico-right.png) right no-repeat;
		background-size: auto 1rem;
		margin-bottom: 2.5rem;
	}
	.bank-logo{
		width: 2rem;
		height: 2rem;
		margin-right: 1rem;
		text-align: center;
	}
	.bank-name{
		font-size: 1rem;
	}
	.bank-tips{
		color: #999;
		margin-top: 0.25rem;
	}
	.withdrawal-amount-title{
		font-size: 1rem;
		color: #666;
		margin: 1rem 0;
	}
	.withdrawal-amount-input{
		font-size: 2rem;
		border-bottom: 1px solid #e5e5e5;
		padding-bottom: 0.8rem;
	}
	.withdrawal-amount-input span{
		margin-left: -0.5rem;
	}
	.withdrawal-amount-input input{
		font-size: 2rem;
		width: 50%;
		margin-left: -0.5rem;
	}
	.withdrawal-amount-tips{
		font-size: 0.75rem;
		color: #999;
		margin-top: 0.5rem;
	}
	.withdrawal-all{
		color: #2986CF;
	}
	.withdrawal-btn{
		background-color: #F2F2F2;
		color: #BBB;
		text-align: center;
		line-height: 2.5rem;
		border-radius: 0.25rem;
		margin-top: 3rem;
	}
	.withdrawal-btn.click{
		background-color: #000;
		color: #fff;
	}
</style>



