<template>
	<div class="mine-setting">
		<ul class="mine-setting-list">
			<li class="mine-setting-items">关于我们</li>
			<router-link tag="li" to="FeedBack" class="mine-setting-items">意见反馈</router-link>
		</ul>
		<div class="log-out" @click="logOut">退出登录</div>
	</div>
</template>

<script>
export default {
	name: 'MineSetting',
	data () {
		return {
			msg:"设置"
		}
	},
	created (){
		document.title="设置";
		window.scrollTo(0, 0);
	},
	mounted () {
		// this.init()
	},
	methods: {
		logOut: function () {
			alert("退出登录")
		}
	}
}
</script>

<style scoped>
	.mine-setting-list{
		padding:0 1rem;
	}
	.mine-setting-items{
		border-bottom: 1px solid #e5e5e5;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		padding: 1rem 0;
	}
	.log-out{
		text-align: center;
		font-size: 1.125rem;
		margin: 3rem;
		font-weight: bold;
	}
</style>



