<template>
	<div class="setting-password">
		<div class="sms-title">验证码已发送至</div>
		<div class="sms-tel">
			<div class="sms-tel-num">{{telephone}}</div>
			<div class="sms-get-code">(60秒)</div>
		</div>
		<div class="code-input"><input type="text" id="code_input" placeholder="输入短信验证码" maxlength="4"></div>
		<div class="password-input">
			<input type="text" id="password1" placeholder="请输入密码为6-16位字符，数字或者字母" maxlength="16">
		</div>
		<div class="password-input">
			<input type="text" id="password2" placeholder="请再次输入上方密码进行确认" maxlength="16">
		</div>
		<div class="register-btn" id="submit_btn" @click="submitFn">确认</div>
	</div>
</template>

<script>
export default {
	name: 'SettingPassword',
	data () {
		return {
			msg:"注册",
			clickBol:true,
			timer:"1",
			telephone:"",
			type:1
		}
	},
	created (){
		console.log(this.$route.params.type)
		if (this.$route.params.type=="register") {
			document.title="注册";
		}
		if (this.$route.params.type=="setting") {
			document.title="修改密码";
			this.type=2;
		}
		window.scrollTo(0, 0);
	},
	mounted () {
		console.log(this.$route.params.type)
		this.init();
	},
	beforeDestroy(){
		clearInterval(this.timer);
	},
	methods: {
		init: function () {
			var type = this.type;
			this.telephone=this.$route.params.tel;
			var _this = this;
			_this.clickBol=true;
			var time=60;
			var $getCode = $(".sms-get-code");
			$getCode.text("("+time+"秒)").css({color: "#999"})
			_this.timer = setInterval(function(){
				if (time>0) {
					time--;
					$getCode.text("("+time+"秒)");
					console.log(_this.clickBol)
				}else{
					_this.clickBol=false;
					clearInterval(_this.timer);
					$getCode.text("获取验证码").css({color: "#007BF9"}).on('click', function() {
						if (_this.clickBol) return;
						console.log(_this.clickBol)
						var telephone = $(".sms-tel-num").text();
						console.log(type,telephone)
						axios.post("/api/sms/send",{"type":type,"telephone":telephone}).then(res=>{
							console.log(res)
							console.log(res.data.response.data)
							// if (res.data.code==200) {
							// 	this.leagueMatchesList=res.data.data.items;
							// 	$("#matches_endloading").hide();
							// }
							// _this.$router.push({ path: '/SettingPassword/type/register/tel/'+telephone })
						})
						_this.init();
					});;
				}
			},1000);
			$("input").on('input propertychange', function(e) {
				var isInput=0;
				$.each($("input"),function(i,o){
					if ($(o).val()!="") isInput++;
				});
				if (isInput==3) {
					$("#submit_btn").css({
						"background-color": '#000',
						"color": '#fff'
					})
				}else{
					$("#submit_btn").css({
						"background-color": '#BEBEBE',
						"color": '#999'
					})
				};
			});
		},
		submitFn:function(){
			console.log(this.type)
			var telephone = this.telephone;
			var password1 = $("#password1").val();
			var password2 = $("#password2").val();
			var code = $("#code_input").val();

			if (code=="") {CF.showTips("验证码不能为空");return;}
			if (password1 =="" || password1.length<6) {CF.showTips("密码长度需大于6位字符");return;}
			if (password1 != password2) {CF.showTips("密码不一致");return;}


			if (this.type==1) {
				axios.post("/api/register",{"telephone":telephone,"password":password1,"verify_code":code}).then(res=>{
					if (res) {
						console.log(res)
						if (res.data.code==1001) {
							CF.showTips(res.data.msg)
							setTimeout(this.$router.push({ path: '/Login' }),1000);
						}
					}
				})
			}else{
				axios.post("/api/forget",{"telephone":telephone,"password":password1,"verify_code":code}).then(res=>{
					if (res) {
						console.log(res)
						if (res.data.code==1001) {
							CF.showTips(res.data.msg);
							setTimeout(this.$router.push({ path: '/Login' }),1000);
						}
					}
				})
			}
		}
	}
}
</script>

<style scoped>
	input{
		width: 100%;
	}
	.setting-password{
		padding: 1rem;
	}
	.setting-password>div{
		margin: 3rem 0;
	}
	.setting-password .sms-title{
		margin: 2rem 0;
		font-size: 1.5rem;
	}
	.setting-password .sms-tel{
		margin: 2rem 0;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		align-items: center;
		-webkit-align-items: center;
	}
	.sms-get-code{
		color: #999;
	}
	.register-btn{
		background-color: #EBEBEB;
		color: #999;
		padding: 1rem;
		text-align: center;
	}
</style>



