<template>
	<div class="feed-back">
		<textarea name="" id=""></textarea>
		<div class="submit" @click="submitFn">提交</div>
	</div>
</template>

<script>
export default {
	name: 'FeedBack',
	data () {
		return {
			msg:"反馈"
		}
	},
	created (){
		document.title="反馈";
		window.scrollTo(0, 0);
	},
	mounted () {
		$("textarea").focus()
	},
	methods: {
		submitFn:function () {
			var content = $("textarea").val();
			axios.post("/api/feedback",{"content":content}).then(res=>{
				console.log(res)
				console.log(res.data.response.data)
				// if (res.data.code==200) {
				// 	this.leagueMatchesList=res.data.data.items;
				// 	$("#matches_endloading").hide();
				// }
				// _this.$router.push({ path: '/SettingPassword/type/register/tel/'+telephone })
			})
		}
	}
}
</script>

<style scoped>
	.feed-back{
		text-align: center;
	}
	textarea{
		width: 96%;
		height: 12rem;
		resize:none;
		padding: 1rem;
		background-color: #F3F3F3;
		box-sizing: border-box;
		margin: 0.5rem 0;
	}
	.submit{
		width: 40%;
		font-size: 1.125rem;
		text-align: center;
		margin: 1rem auto;
		background-color: #000;
		color: #fff;
		padding: 0.5rem 0;
		border-radius: 0.25rem;
	}
</style>



