<template>
	<div class="mine-collect">
		<ul class="goods-list">
			<li class="goods-items">
				<div class="designer-laber">PRO</div>
				<div class="select" v-show="isShow" @click="selectFn($event)">
					<div class="ico">√</div>
				</div>
				<router-link tag="div" :to="{ path: 'GoodsDetail/gid/1'}" class="goods-details">
					<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
					<div><img class="designer-header" src="../assets/ico_header.png"></div>
					<span class="goods-title">戏子</span>
					<p class="goods-price">￥10</p>
				</router-link>
			</li>
			<li class="goods-items">
				<div class="designer-laber">PRO</div>
				<div class="select" v-show="isShow" @click="selectFn($event)">
					<div class="ico">√</div>
				</div>
				<router-link tag="div" :to="{ path: 'GoodsDetail/gid/1'}" class="goods-details">
					<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
					<div><img class="designer-header" src="../assets/ico_header.png"></div>
					<span class="goods-title">戏子</span>
					<p class="goods-price">￥10</p>
				</router-link>
			</li>
			<li class="goods-items">
				<div class="designer-laber">PRO</div>
				<div class="select" v-show="isShow" @click="selectFn($event)">
					<div class="ico">√</div>
				</div>
				<router-link tag="div" :to="{ path: 'GoodsDetail/gid/1'}" class="goods-details">
					<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
					<div><img class="designer-header" src="../assets/ico_header.png"></div>
					<span class="goods-title">戏子</span>
					<p class="goods-price">￥10</p>
				</router-link>
			</li>
			<li class="goods-items">
				<div class="designer-laber">PRO</div>
				<div class="select" v-show="isShow" @click="selectFn($event)">
					<div class="ico">√</div>
				</div>
				<router-link tag="div" :to="{ path: 'GoodsDetail/gid/1'}" class="goods-details">
					<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
					<div><img class="designer-header" src="../assets/ico_header.png"></div>
					<span class="goods-title">戏子</span>
					<p class="goods-price">￥10</p>
				</router-link>
			</li>
		</ul>
		<div class="edit-btn" v-show="!isShow" id="edit_btn" @click="isShow=!isShow">编辑</div>
		<div class="edit-btn" v-show="isShow" id="del_btn" @click="delFn">删除选中商品</div>
	</div>
</template>

<script>
export default {
	name: 'MineCollect',
	data () {
		return {
			msg:"反馈",
			isShow:false,
		}
	},
	created (){
		document.title="商品收藏";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		delFn:function(){
			$(".isSelect").parent().parent().remove();
			this.isShow=!this.isShow
		},
		selectFn:function(e){
			$(e.target).find(".ico").toggleClass('isSelect');
		}
	}
}
</script>

<style scoped>
	.goods-list{display: flex;display: -webkit-flex;flex-wrap: wrap;-webkit-flex-wrap: wrap;justify-content: space-between;-webkit-justify-content: space-between;padding: 1rem;}
	.goods-items{width: 48%;padding: 1.5rem;font-size: 0.75rem;box-sizing: border-box;flex-shrink:0;-webkit-flex-shrink:0;text-align: center;position: relative;}
	.designer-laber{font-size: 0.75rem;background-color: #000;color: white;padding: 0 0.2rem;position: absolute;top: 0;right: 0;}
	.goods-details{background: url("../assets/clothes-bg.png") top no-repeat;background-size: 100% auto;}
	.goods-logo{width: 42%;margin-top: 1.8rem;}
	.designer-header{width: 18%;margin-top: 0.5rem;}
	.select{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}
	.select .ico{
		width: 1rem;
		height: 1rem;
		border-radius: 0.5rem;
		border:1px solid #000;
		color: #fff;
		position: absolute;top: 0;left: 0;
	}
	.select .ico.isSelect{
		background-color: #000;
	}
	.edit-btn{
		width: 100%;
		box-sizing: border-box;
		text-align: center;
		background-color: #000;
		color: #fff;
		padding: 0.5rem;
		position: fixed;
		left: 0;
		bottom: 0%;
	}
</style>



