<template>
	<div class="mine-coupon">
		<ul class="coupon-tab">
			<li v-for="(value,index) in tabList" :class="{cur:active==index}" @click="tabFn(index)">{{value}}</li>
		</ul>
		<ul class="coupon-list" id="unused_list" v-show="active==0">
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused">
						<div class="used-btn">立即使用</div>
					</div>
				</div>
			</li>
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused">
						<div class="used-btn">立即使用</div>
					</div>
				</div>
			</li>
		</ul>
		<ul class="coupon-list" id="used_list" v-show="active==1">
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused"></div>
				</div>
			</li>
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused"></div>
				</div>
			</li>
		</ul>
		<ul class="coupon-list" id="expired_list" v-show="active==2">
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused"></div>
				</div>
			</li>
			<li class="coupon-items">
				<div class="coupon-select hide" @click="selectFn($event)"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused"></div>
				</div>
			</li>
		</ul>
		<div class="compile-btn" id="compile_btn" @click="compileFn($event)">编辑</div>
		<div class="del-box">
			<div class="del-left">
				<div class="all-select" @click="allSelectFn">
					<div class="coupon-select"></div>
					<div>全选</div>
				</div>
				<div class="cancel" @click="cancelFn">取消</div>
			</div>
			<div class="del-right">批量删除({{selectNum}})</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'MineCoupon',
	data () {
		return {
			tabList:["未使用","已使用","已过期"],
			active:0,
			selectNum:0,
			allSelect:false,
		}
	},
	created (){
		document.title="优惠券";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {
			axios.get("/api/coupon/mine",{"status":1}).then(res=>{
				console.log(res)
				console.log(res.data.response.data)
				// if (res.data.code==200) {
				// 	this.leagueMatchesList=res.data.data.items;
				// 	$("#matches_endloading").hide();
				// }
			})
		},
		tabFn:function(index){
			this.active=index;
			$("#compile_btn").show();
			$(".coupon-items").css({width: '100%'});
			$(".coupon-select").hide();
			$(".coupon-select").removeClass('coupon-isSelect');
		},
		compileFn:function(e){
			$(e.target).hide();
			$(".coupon-items").css({width: '104%'});
			$(".coupon-select").show();
		},
		selectFn:function(e){
			$(e.target).toggleClass('coupon-isSelect');
			this.selectNum=$(".coupon-isSelect").length-$(".all-select .coupon-isSelect").length;
			if (this.selectNum==0) $(".all-select .coupon-isSelect").removeClass('coupon-isSelect');
			if (this.selectNum==$(".coupon-select:visible").length-1){
				$(".all-select .coupon-select").addClass('coupon-isSelect');
				this.allSelect=true;
			}else{
				$(".all-select .coupon-select").removeClass('coupon-isSelect');
				this.allSelect=false;
			}
			console.log(this.selectNum)
			console.log($(".coupon-select:visible").length-1)
		},
		cancelFn:function(){
			$("#compile_btn").show();
			$(".coupon-items").css({width: '100%'});
			$(".coupon-select").hide();
			$(".coupon-select").removeClass('coupon-isSelect');
		},
		allSelectFn:function(){
			this.allSelect=!this.allSelect;
			if (this.allSelect) {
				$(".coupon-select:visible").addClass('coupon-isSelect');
			}else{
				$(".coupon-select:visible").removeClass('coupon-isSelect');
			}
			this.selectNum=$(".coupon-isSelect").length-$(".all-select .coupon-isSelect").length;
		}
	}
}
</script>

<style scoped>
	.mine-coupon{
		margin-bottom: 3rem;
	}
	.coupon-tab{
		display: flex;
		justify-content: space-around;
		display: -webkit-flex;
		-webkit-justify-content: space-around;
		color: #666;
		font-size: 0.875rem;
	}
	.coupon-tab li{
		padding: 0.5rem 0;
	}
	.cur{
		color: #000;
		border-bottom: 2px solid #000;
	}
	.coupon-list{
		padding: 0.5rem;
	}
	.coupon-items{
		padding: 0.5rem;
		display: flex;
		display: -webkit-flex;
		align-items: center;
	}
	.coupon-box{
		width: 100%;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.coupon-num{
		position: relative;
		display: flex;
	}
	.coupon-num-bg{
		width: 7rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.coupon-value{
		width: 7rem;
		height: 5.5rem;
		line-height: 5.5rem;
		text-align: center;
		position: relative;
		color: #fff;
		font-size: 1.25rem;
	}
	.coupon-value-price{
		font-size: 2.5rem;
		margin-left: -0.5rem;
	}
	.coupon-con{
		display: -webkit-flex;
		-webkit-flex-direction:column;
		-webkit-justify-content: space-between;
		display: flex;
		flex-direction:column;
		justify-content: space-between;
		font-size: 0.75rem;
		padding: 0.5rem;
	}
	.coupon-isused{
		display: flex;
		display: -webkit-flex;
		flex-direction:column-reverse;
		-webkit-flex-direction:column-reverse;
	}
	#used_list .coupon-isused{
		width: 4rem;
		background: url(../assets/coupon-used.png) no-repeat 0.5rem -0.8rem;
		background-size: 100% auto;
	}
	#expired_list .coupon-isused{
		width: 4rem;
		background: url(../assets/coupon-expired.png) no-repeat 0.5rem -0.8rem;
		background-size: 100% auto;
	}
	.coupon-select{
		width: 1rem;
		height: 1rem;
		margin-right: 0.5rem;
		background: url(../assets/select1.png) no-repeat;
		background-size: 100% 100%;
	}
	.coupon-isSelect{
		background: url(../assets/select2.png) no-repeat;
		background-size: 100% 100%;
	}
	.used-btn{
		font-size: 0.75rem;
		border:1px solid #000;
		border-radius: 1rem;
		padding: 0.1rem 0.5rem;
		margin-bottom: 0.5rem;
	}
	.coupon-title{
		color: #666;
	}
	.coupon-date{
		color: #999;
	}
	.compile-btn{
		width: 100%;
		background-color: #000;
		color: #fff;
		text-align: center;
		position: fixed;
		bottom: 0;
		left: 0;
		line-height: 3rem;
		z-index: 99;
	}
	.del-box{
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		font-size: 0.875rem;
		border-top: 1px solid #e5e5e5;
		z-index: 98;
	}
	.del-left{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		width: 75%;
		padding: 0.5rem;
	}
	.all-select{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		padding-left: 0.5rem;
	}
	.del-right{
		width: 25%;
		text-align: center;
		padding: 0.5rem 0;
		background-color: #000;
		color: #fff;
	}
</style>



