<template>
	<div class="application-designer-message">
		<ul class="input-list">
			<li><input type="text" placeholder="真实姓名"></li>
			<li><input type="text" placeholder="身份证号"></li>
			<li><input type="text" placeholder="行业分类"></li>
			<li><input type="text" placeholder="职业名称"></li>
		</ul>
		<div class="file-wrap">
			<div class="title">身份证明与作品</div>
			<div class="tips">提示：上传工牌，工作证，名片，职业资格证，在职劳动合同等</div>
			<div class="file-box">
				<div class="camera" @click="addFile"><img src="../assets/ico_camera.png"></div>
			</div>
		</div>
		<div class="designer-tcp">
			<div class="dagou" :class="dagouId?'isSelect':''" @click="dagouId=!dagouId"></div>
			<div class="tcp">已阅读并同意ONET设计师协议</div>
		</div>
		<div class="submit">确定</div>
	</div>
</template>

<script>
export default {
	name: 'ApplicationDesignerMessage',
	data () {
		return {
			fileInputNum:1,
			dagouId:false
		}
	},
	created (){
		document.title="认证设计师申请信息";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		addFile:function(){
			var _this=this;
			var fileInputNum = this.fileInputNum;
			if ($("#file_input"+fileInputNum).length>0) $("#file_input"+fileInputNum).remove();
			if (fileInputNum>9) return;
			var fileInput = '<input class="hide" id="file_input'+fileInputNum+'" type="file" accept="image/*">'
			$(".file-wrap").append(fileInput);
			$(fileInput).click().on("change",function() {
				console.log(fileInputNum)
				_this.fileInputNum++;
				var URL = window.URL || window.webkitURL;
			    var imgURL = URL.createObjectURL(this.files[0]);
				var fileImg='<div class="file-img" id="file_img'+fileInputNum+'"></div>'
				var $elm = $(fileImg)
				$(".file-box").prepend($elm);
				var elmHeight = $elm.outerWidth();
				$($elm).css({
					'height':elmHeight,
					'background': 'url('+imgURL+') center no-repeat',
					'background-size': 'cover',
					'background-clip':'content-box'
				});
				if (_this.fileInputNum>9) {
					$(".camera").hide();
				}
			});
		},
	}
}
</script>

<style>
	.application-designer-message{
		padding: 1rem;
		font-size: 0.875rem;
	}
	.input-list li{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;

	}
	.input-list input{
		font-size: 0.875rem;
	}
	.file-wrap{
		padding: 1rem 0;
		color: #666;
	}
	.tips{
		font-size: 0.75rem;
		line-height: 1.5rem;
		margin-bottom: 1rem;
	}
	.file-box{
		display: flex;
		display: -webkit-flex;
		flex-wrap: wrap;
		-webkit-flex-wrap: wrap;
	}
	.file-box>div{
		flex-shrink:0;
		-webkit-flex-shrink:0;
	}
	.camera{
		width: 4.6rem;
		height: 4.6rem;
		padding: 1.5rem;
		margin: 0.5rem 0.4rem;
		box-sizing: border-box;
		border:1px dashed #e5e5e5;
	}
	.file-img{
		width: 25%;
		padding: 0.5rem;
		box-sizing: border-box;
	}
	.designer-tcp{
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		-webkit-justify-content: center;
		align-items: center;
		-webkit-align-items: center;
	}
	.dagou{
		width: 1.5rem;
		height: 1rem;
		background: url("../assets/ico_gou1.png") left no-repeat;
		background-size: auto 1rem;
	}
	.isSelect{
		background: url("../assets/ico_gou2.png") left no-repeat;
		background-size: auto 1rem;
	}
	.dagou img{
		width: auto;
		height: 1rem;
	}
	.submit{
		text-align: center;
		background-color: #000;
		color: #fff;
		line-height: 2.5rem;
		border-radius: 0.25rem;
		margin-top: 1rem;
	}
</style>



