<script>
import userAvatar from '../assets/ico_header.png'
export default {
	name: 'designer',
	data () {
		return {
			designerList:"",
			userAvatar:userAvatar
		}
	},
	created (){
		document.title="设计师";
	},
	mounted:function(){
		// this.$nextTick(function(){
		// 	this.designerPage();
		// })
		this.getData();
	},
	methods:{
		getData:function(){
			axios.get("/api/user/designer").then(res=>{
				console.log(res)
				if (res.data.code==1001) {
					this.designerList=res.data.response.data;
				}
			});
		}
	},
}
</script>
<style scoped>
	.designer-list{
		padding: 0 0.8rem;
	}
	.designer-items{
		border-bottom: 1px solid #e5e5e5;
		padding-top: 1rem;
	}
	.designer-title{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
	}
	.designer-header{
		width: 2.5rem;
		margin-right: 0.5rem;
	}
	.designer-value{
		display: flex;
		display: -webkit-flex;
		justify-content: space-around;
		-webkit-justify-content: space-around;
		margin:1rem 0;
	}
	.designer-name{
		font-size: 0.875rem;
	}
	.designer-post{
		color: #999;
		font-size: 0.75rem;
	}
	.designer-works{
		background: url("../assets/clothes-bg.png") top no-repeat;
		background-size: 100% auto;
		text-align: center;
		margin: 0 1rem;
	}
	.goods-logo{
		width: 40%;
		margin: 1.5rem 0 1rem;
	}
</style>



