<template>
	<div class="upload-works">
		<div class="works-icon">
			<div class="file-btn" @click="addFile">点击上传图片</div>
		</div>
		<ul class="list">
			<li class="items border-bottom">
				<span>作品售价</span>
				<span>{{price|formatMoney}}</span>
			</li>
			<!--  class="backspread" -->
			<li class="items backspread">
				<span>单件收益</span>
				<div class="">
					<span>{{backspread|formatMoney}}</span>
					<select ref="select" v-model="backspread" value="" @change="changeFn">
						<option v-for="v in backspreadList" :value="v">{{v}}</option>
						<!-- <option value="19">19</option>
						<option value="29">29</option>
						<option value="39">39</option> -->
					</select>
				</div>
			</li>
		</ul>
		<div class="works-name">
			<input type="text" placeholder="请输入作品名字" maxlength="12">
			<div class="word-number">(0/12)</div>
		</div>
		<div class="design-inspiration">
			<textarea class="" name="" id="" placeholder="说说你的设计灵感"></textarea>
			<div class="word-number">至少输入50个字</div>
		</div>
		<div class="submit">发布</div>
	</div>
</template>

<script>
export default {
	name: 'UploadWorks',
	filters:{
		formatMoney: function(val){
			return "￥" + val;
		}
	},
	data () {
		return {
			price:79,
			backspread:9,
			priceList:[79,89,99,109],
			backspreadList:[9,19,29,39]
		}
	},
	created (){
		document.title="上传作品";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		addFile:function(){
			var fileInput = '<input class="hide" id="file_input" type="file" accept="image/*">'
			$(".upload-works").append(fileInput);
			$(fileInput).click().on("change",function() {
				var URL = window.URL || window.webkitURL;
			    var imgURL = URL.createObjectURL(this.files[0]);
			    var fileImg = '<img src="'+imgURL+'">'
				$(".file-btn").html(fileImg);
			});
		},
		changeFn:function(){
			this.price=this.priceList[$("option:selected").index()];
		}
	}
}
</script>

<style scoped>
	.upload-works{
		padding: 1rem;
		font-size: 0.875rem;
	}
	.works-icon{
		width: 100%;
		height: 16rem;
		background: url("../assets/clothes-bg.png") center no-repeat;
		background-size: 68% auto;
		margin-bottom: 1rem;
	}
	.file-btn{
		width: 6rem;
		height: 9rem;
		margin: auto;
		padding-top: 4rem;
		text-align: center;
		line-height: 9rem;
		color: #999;
	}
	.items{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		justify-content: -webkit-space-between;
		padding:0.5rem 0;
	}
	.items.backspread{
		background: url(../assets/ico-right.png) right no-repeat;
		background-size: auto 0.75rem;
		padding-right: 1rem;
		position: relative;
	}
	.items select{
		font-size: 0.875rem;
		opacity: 0;
		position: absolute;
		top: 0.5rem;
		right: 0;
		width: 100%;
		height: 1rem;
	}
	.works-name{
		padding-bottom: 1rem;
	}
	.works-name input{
		width: 100%;
		 font-size: 0.875rem;
		padding-top: 1rem;
	}
	.design-inspiration textarea{
		width: 100%;
		height: 6rem;
		font-size: 0.875rem;
		margin-bottom: 1rem;
		resize:none;
		outline: none;
	}
	.word-number{
		font-size: 0.75rem;
		color: #999;
		text-align: right;
	}
	.submit{
		text-align: center;
		background-color: #000;
		color: #fff;
		line-height: 2.5rem;
		border-radius: 0.25rem;
		margin-top: 2rem;
	}
</style>



