<template>
	<div class="bank-card">
		<ul class="bank-card-list">
			<li class="bank-card-items">
				<div class="bank-card-name">
					<div class="icon"><img src="../assets/ico-bank-js.png"></div>
					<div class="text">
						<p>建设银行</p>
						<p>储蓄卡</p>
					</div>
				</div>
				<div class="bank-card-num">**** **** **** 0728</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'BankCard',
	filters:{
		formatMoney: function(val){
			return "￥" + val.toFixed(2);
		}
	},
	data () {
		return {

		}
	},
	created (){
		document.title="银行卡";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	watch:{

　　},
	methods: {
		withdrawalFn:function(){
			if(this.inputVal>=50) this.$router.push({ path: '/WithdrawalDetails' })
		}
	}
}
</script>

<style scoped>
	.bank-card-items{
		margin: 1rem;
		background: url(../assets/bg-bank-js.png) right no-repeat;
		background-size: 30% auto;
	}
	.bank-card-name{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		align-items: -webkit-center;
	}
	.icon{
		width: 2rem;
		height: 2rem;
		padding: 0.5rem;
	}
	.icon img{
		display: block;
	}
	.text{
		font-size: 0.938rem;
	}
	.bank-card-num{
		text-align: right;
		font-size: 1.4rem;
		padding: 0.5rem;
	}
</style>



