<template>
	<div class="mine-income">
		<div class="income-balance">{{balance}}</div>
		<div class="income-unit">当前余额 (元)</div>
		<div class="income-details">
			<div class="income-details-left">
				<p class="income-details-num">178</p>
				<p class="income-details-unit">总收益(元)</p>
			</div>
			<div class="income-details-right">
				<p class="income-details-num">10</p>
				<p class="income-details-unit">已卖出(件)</p>
			</div>
		</div>
		<div class="withdrawal-button" :class="{click:balance>=50}" @click="toWithdrawal">提现</div>
		<div class="withdrawal-tips">金额超过50元可提现</div>
	</div>
</template>

<script>
export default {
	name: 'MineIncome',
	data () {
		return {
			balance:58
		}
	},
	created (){
		document.title="我的收益";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		toWithdrawal:function () {
			if (this.balance>=50) this.$router.push({ path: '/Withdrawal' })
		}
	}
}
</script>

<style scoped>
	.mine-income{
		text-align: center;
		padding: 1rem;
		font-size: 0.875rem;
		background: url("../assets/bg-income.png") 0 -3.5rem no-repeat;
		background-size: 100% auto;
	}
	.income-balance{
		font-size: 4rem;
		margin-top: 2.5rem;
	}
	.income-unit{
		margin-top: 0.5rem;
	}
	.income-details{
		display: flex;
		display: -webkit-flex;
		justify-content: space-around;
		justify-content: -webkit-space-around;
		margin: 3.5rem 0;
		background: url("../assets/ico-right.png") 92% no-repeat;
		background-size: auto 1rem;
	}
	.income-details-num{
		font-size: 1.2rem;
		margin-bottom: 0.3rem;
	}
	.income-details-unit{
		color: #666;
	}
	.withdrawal-button{
		background-color: #F2F2F2;
		color: #BBB;
		line-height: 2.5rem;
		border-radius: 0.25rem;
		margin: 0.5rem;
	}
	.withdrawal-button.click{
		background-color: #000;
		color: #fff;
	}
	.withdrawal-tips{
		color: #999;
	}
</style>



