import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Search from '@/pages/Search'
import SearchMore from '@/pages/Search-more'
import Subject from '@/pages/Subject'
import Designer from '@/pages/Designer'
import GoodsDetail from '@/pages/Goods-detail'
import DesignerDetail from '@/pages/Designer-detail'
import ConfirmOrder from '@/pages/Confirm-order'
import CompletePay from '@/pages/Complete-pay'
import Mine from '@/pages/Mine'
import MineData from '@/pages/Mine-data'
import MineSetting from '@/pages/Mine-setting'
import FeedBack from '@/pages/Mine-feed-back'
import MineCollect from '@/pages/Mine-collect'
import MineChangepsw from '@/pages/Mine-changepsw'
import MineCoupon from '@/pages/Mine-coupon'
import MineOrder from '@/pages/Mine-order'
import MineOrderDetails from '@/pages/Mine-order-details'
import MineWorks from '@/pages/Mine-works'
import MineWorksDetails from '@/pages/Mine-works-details'
import MineIncome from '@/pages/Mine-income'
import BankCard from '@/pages/Bank-card'
import Withdrawal from '@/pages/withdrawal'
import WithdrawalDetails from '@/pages/withdrawal-details'
import UploadWorks from '@/pages/Upload-works'
import SettingSign from '@/pages/Mine-setting-sign'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import SettingPassword from '@/pages/Setting-password'
import AfterSales from '@/pages/After-sales'
import AfterSalesSubmit from '@/pages/After-sales-submit'
import RefundDetails from '@/pages/Refund-details'
import ApplicationDesigner from '@/pages/Application-designer'
import ApplicationDesignerMessage from '@/pages/Application-designer-message'

import Demo from '@/pages/Demo'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home,
		},
		{
			path: '/Search',
			name: 'Search',
			component: Search,
		},
		{
			path: '/SearchMore/type/:type',
			name: 'SearchMore',
			component: SearchMore,
		},
		{
			path: '/Subject/sid/:sid',
			name: 'Subject',
			component: Subject,
		},
		{
			path: '/Designer',
			name: 'Designer',
			component: Designer,
		},
		{
			path: '/GoodsDetail/gid/:gid',
			name: 'GoodsDetail',
			component: GoodsDetail,
		},
		{
			path: '/DesignerDetail/did/:did',
			name: 'DesignerDetail',
			component: DesignerDetail,
		},
		{
			path: '/ConfirmOrder',
			name: 'ConfirmOrder',
			component: ConfirmOrder,
		},
		{
			path: '/CompletePay',
			name: 'CompletePay',
			component: CompletePay,
		},
		{
			path: '/Mine',
			name: 'Mine',
			component: Mine,
		},
		{
			path: '/MineData',
			name: 'MineData',
			component: MineData,
		},
		{
			path: '/MineSetting',
			name: 'MineSetting',
			component: MineSetting,
		},
		{
			path: '/MineOrder',
			name: 'MineOrder',
			component: MineOrder,
		},
		{
			path: '/MineOrderDetails/type/:type',
			name: 'MineOrderDetails',
			component: MineOrderDetails,
		},
		{
			path: '/FeedBack',
			name: 'FeedBack',
			component: FeedBack,
		},
		{
			path: '/MineCollect',
			name: 'MineCollect',
			component: MineCollect,
		},
		{
			path: '/MineChangepsw',
			name: 'MineChangepsw',
			component: MineChangepsw,
		},
		{
			path: '/MineCoupon',
			name: 'MineCoupon',
			component: MineCoupon,
		},
		{
			path: '/MineWorks',
			name: 'MineWorks',
			component: MineWorks,
		},
		{
			path: '/MineWorksDetails',
			name: 'MineWorksDetails',
			component: MineWorksDetails,
		},
		{
			path: '/MineIncome',
			name: 'MineIncome',
			component: MineIncome,
		},
		{
			path: '/BankCard',
			name: 'BankCard',
			component: BankCard,
		},
		{
			path: '/Withdrawal',
			name: 'Withdrawal',
			component: Withdrawal,
		},
		{
			path: '/WithdrawalDetails',
			name: 'WithdrawalDetails',
			component: WithdrawalDetails,
		},
		{
			path: '/UploadWorks',
			name: 'UploadWorks',
			component: UploadWorks,
		},
		{
			path: '/SettingSign',
			name: 'SettingSign',
			component: SettingSign,
		},
		{
			path: '/Login',
			name: 'Login',
			component: Login,
		},
		{
			path: '/Register/type/:type',
			name: 'Register',
			component: Register,
		},
		{
			path: '/SettingPassword/type/:type/tel/:tel',
			name: 'SettingPassword',
			component: SettingPassword,
		},
		{
			path: '/AfterSales',
			name: 'AfterSales',
			component: AfterSales,
		},
		{
			path: '/AfterSalesSubmit',
			name: 'AfterSalesSubmit',
			component: AfterSalesSubmit,
		},
		{
			path: '/RefundDetails',
			name: 'RefundDetails',
			component: RefundDetails,
		},
		{
			path: '/ApplicationDesigner',
			name: 'ApplicationDesigner',
			component: ApplicationDesigner,
		},
		{
			path: '/ApplicationDesignerMessage',
			name: 'ApplicationDesignerMessage',
			component: ApplicationDesignerMessage,
		},
		
		{
			path: '/Demo',
			name: 'Demo',
			component: Demo,
		}
	]
})



