<template>
	<div class="mine-works-details">
		<div class="works-icon">
			<img src="../assets/goods-logo.png">
		</div>
		<div class="works-introduce">
			<div class="works-title">简约而不简单</div>
			<div class="works-sales">(已售卖5件)</div>
			<div class="works-describe">这个图形设计经典，阿呆好久哦已够华东师范开几号发货点卡收费计划的喀什，是肯定就发估计是电饭锅会计师v火炬大厦广发华福卡卡是个大赛科技分行贷款是否该地块建安费好可怜放假后副科级大理石就发大师傅</div>
			<div class="works-price">￥89.00</div>
		</div>
		<div class="graphic-details"><img src="../assets/graphic.png"></div>

		<div class="space"></div>
		<div class="btn-bottom">
			<div class="btn-left">撤销售卖</div>
			<div class="btn-right">立即购买</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'MineWorksDetails',
	data () {
		return {

		}
	},
	created (){
		document.title="作品";
		window.scrollTo(0, 0);
	},
	mounted () {
		
	},
	methods: {

	}
}
</script>

<style scoped>
	.works-icon{
		width: 100%;
		height: 22rem;
		background: url("../assets/clothes-bg.png") center no-repeat;
		background-size: auto 100%;
		margin: 1rem 0 0.5rem;
		position: relative;
	}
	.works-icon img{
		width: 38%;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		margin: 5rem auto;
	}
	.works-introduce{
		text-align: center;
		font-size: 0.875rem;
		padding: 1rem;
	}
	.works-title,.works-price{
		font-size: 1rem;
		font-weight: bold;
	}
	.works-sales{
		color: #666;
		margin: 0.5rem 0;
	}
	.works-describe{
		color: #999;
		margin-bottom: 0.5rem;
	}
	.space{
		height: 5rem;
	}
	.btn-bottom{
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
		display: flex;
		display: -webkit-flex;
		background-color: #fff;
	}
	.btn-bottom div{
		width: 50%;
		margin: 1rem;
		text-align: center;
		border:1px solid #000;
		border-radius: 0.25rem;
		line-height: 2rem;
	}
	.btn-right{
		color: #fff;
		background-color: #000;
	}
</style>



