<template>
	<div class="withdrawal-details">
		<div class="withdrawal-status">
			<div class="icon"><img src="../assets/clock.png"></div>
			<p>提现申请已提交</p>
		</div>
		<ul class="withdrawal-details-list">
			<li class="items" v-for="(v,i) in listTitle">
				<div class="items-left">{{v}}</div>
				<div class="items-right">{{listValue[i]}}</div>
			</li>
		</ul>
		<router-link tag="div" to="/MineIncome" class="complete-btn">完成</router-link tag="div" to="MineIncome">
	</div>
</template>

<script>
export default {
	name: 'WithdrawalDetails',
	filters:{
		formatMoney: function(val){
			return "￥" + val.toFixed(2);
		}
	},
	data () {
		return {
			listTitle:["提现时间","储蓄卡","流水号","提现金额","手续费"],
			listValue:[2017,"建设银行",465465465465,58,0.58],
		}
	},
	created (){
		document.title="提现详情";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	watch:{

　　},
	methods: {

	}
}
</script>

<style scoped>
	.withdrawal-details{
		padding: 1rem;
	}
	.withdrawal-status{
		text-align: center;
		margin-bottom: 3rem;
	}
	.icon img{
		width: 24%;
		margin: 1rem;
	}
	.withdrawal-status p{
		font-weight: bold;
	}
	.items{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		margin: 0.8rem 0;
		font-size: 0.875rem;
	}
	.items-left{
		color:#999;
	}
	.complete-btn{
		text-align: center;
		background-color: #000;
		color: #fff;
		line-height: 2.5rem;
		border-radius: 0.25rem;
		margin-top: 3rem;
	}
</style>



