<template>
	<div class="refund-details">
		<div class="refund-status">
			<span>退款成功</span>
		</div>
		<div class="refund-num">
			<span class="refund-unit">￥</span>
			<span class="refund-int">89</span>
			<span class="refund-float">.00</span>
		</div>
		<ul class="refund-list">
			<li class="refund-whereabouts">
				<div class="refund-title">钱款去向</div>
				<div class="refund-value">我的支付宝</div>
			</li>
			<li class="refund-type">
				<div class="refund-title">售后类型</div>
				<div class="refund-value">退款退货</div>
			</li>
			<li class="refund-sum">
				<div class="refund-title">退款金额</div>
				<div class="refund-value">￥89.00</div>
			</li>
			<li class="refund-cause">
				<div class="refund-title">退款原因</div>
				<div class="refund-value">打印模糊不清</div>
			</li>
			<li class="refund-time">
				<div class="refund-title">申请时间</div>
				<div class="refund-value">2017-04-13 15:15</div>
			</li>
			<li class="refund-site">
				<div class="refund-title">退货地址</div>
				<div class="refund-value">广州市海珠区工业大道313号东方红创意园东7栋2楼，收件人：jason，电话：020-36249580</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'RefundDetails',
	data () {
		return {
		}
	},
	created (){
		document.title="退款详情";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.refund-details{

	}
	.refund-status{
		text-align: center;
		margin-top: 3rem;
	}
	.refund-status span{
		padding-left: 1.5rem;
		background: url(../assets/ico_refund2.png) left no-repeat;
		background-size: auto 1rem;
	}
	.refund-num{
		text-align: center;
		margin-top: 0.5rem;
		margin-bottom: 2rem;
		color: #E50707;
		font-size: 0.875rem;
		font-weight: bold;
	}
	.refund-int{
		font-size: 1.5rem;
		margin-right: -0.3rem;
	}
	.refund-list li{
		display: flex;
		display: -webkit-flex;
		padding: 0.8rem 1rem;
		font-size: 0.875rem;
	}
	.refund-title{
		width: 4rem;
		flex-shrink:0;
		-webkit-flex-shrink:0;
		margin-right: 0.5rem;
		color: #999;
	}
</style>



