<template>
	<div class="login">
		<ul class="login-list">
			<li class="login-items">
				<input ref="phone_num" type="tel" placeholder="手机号码" :value="phone | num" @keyup="phoneChange" maxlength="11">
				<!-- <div class="fail-tips hide">找不到该用户</div> -->
			</li>
			<li class="login-items login-password">
				<div class="login-items-left">
					<input ref="password" class="password-input" type="password" placeholder="密码" onkeyup="this.value=this.value.replace(/\s+/g,'')">
				</div>
				<div class="login-items-right hide" id="toggle_psw_btn" @click="togglePswFn()">{{togglePsw?"隐藏":"显示"}}</div>
			</li>
		</ul>
		<router-link tag="div" to="/Register/type/setting" class="forget-password">忘记密码？</router-link>
		<div class="login-btn" id="submit_btn" @click="loginFn">登录</div>
	</div>
</template>

<script>
export default {
	name: 'Login',
	filters: {
		"num": function(value) {
			return value.replace(/[^\d]/g, '')
		}
	},
	data () {
		return {
			phone:"",
			togglePsw:false,
		}
	},
	created (){
		document.title="登录";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		phoneChange(e) {
			this.phone = e.target.value
			this.$forceUpdate()  // 这里必须有
		},
		init: function () {
			var _this = this;
			var telephone = this.$refs['phone_num'].value;
			$("input").on('input propertychange', function(e) {
				// $(".fail-tips").hide();
				var isInput=0;
				$.each($("input"),function(i,o){
					if ($(o).val()!="") isInput++;
				})
				if (isInput==2) {
					$("#submit_btn").css({
						"background-color": '#000',
						"color": '#fff'
					}).on('click', function() {
						// if (telephone.length!=11) {$(".fail-tips").show();return;};
						// _this.$router.push({ path: '/' })
					})
				}else{
					$("#submit_btn").css({
						"background-color": '#EBEBEB',
						"color": '#999'
					})
				};
			});
			$("#password").on('input propertychange', function(e) {
				console.log($(e.target).val().length)
				if ($(e.target).val().length>0) {
					$("#toggle_psw_btn").show();
				}else{
					console.log($(e.target).val().length)
					$("#toggle_psw_btn").hide();
				}
			});
		},
		togglePswFn:function(){
			this.togglePsw=!this.togglePsw;
			if (this.togglePsw) {
				$("#password").attr("type","text");
			}else{
				$("#password").attr("type","password");
			}
		},
		loginFn:function(){
			CF.showLoading(1);
			var telephone = this.$refs['phone_num'].value;
			var password = this.$refs['password'].value;
			axios.post("/api/login",{"telephone":telephone,"password":password}).then(res=>{
				if (res) {
					console.log(res)
					if (res.data.code==1001) {
						CF.showLoading(false);
						CF.showTips("登录成功...")
						CF.localData("onet_userData",JSON.stringify(res.data.response.data));
						CF.localData("onet_token",res.data.response.data.token);
						setTimeout(this.$router.push({path:'/'}),1000);
					}else{
						CF.showLoading(false);
						CF.showTips(res.data.msg)
					}
				}else{

				}
			}).catch(function (error) {
				CF.showLoading(false);
				CF.showTips("验证失败")
			});
		}
	}
}
</script>

<style scoped>
	.login{
		padding: 1rem;
	}
	.login-list{
		margin-top: 4rem;
	}
	.login-items{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;
	}
	.login-password{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.login-items input{
		width: 100%;
		font-size: 1rem;
	}
	.fail-tips{
		color: red;
		padding-top: 1rem;
	}
	.login-items-left{
		width: 90%;
	}
	.login-items-right{
		color: #999;
	}
	.forget-password{
		font-size: 0.875rem;
		padding: 1rem 0;
	}
	.login-btn{
		background-color: #EBEBEB;
		text-align: center;
		padding: 1rem;
		color: #999;
	}
</style>



