<template>
	<div class="mine-order">
		<ul class="tab-bar">
			<li v-for="(value,index) in tabList" :class="{cur:active==index}" @click="tabFn(index)">{{value}}</li>
		</ul>
		<ul class="all-order-list">
			<router-link v-show="active==0||active==4" tag="li" to="/MineOrderDetails/type/4" class="order-items bottom-space">
				<div class="order-top">
					<span class="order-num">订单号：210480431068</span>
					<span class="order-status"></span>
				</div>
				<div class="order-center">
					<div class="order-icon"></div>
					<div class="order-details">
						<p class="order-title">席子</p>
						<div class="order-style">
							<p>颜色:白色，尺码:L，类型:经典款</p>
							<p>x1</p>
						</div>
						<div class="order-price">￥99</div>
					</div>
				</div>
				<div class="after-sales-status">退货退款 退款成功</div>
				<div class="order-btn">
					<div class="btn-left" data-type="check" @click.stop="orderBtnFn($event)">查看详情</div>
					<div class="btn-right" v-if="show"></div>
				</div>
			</router-link>
			<router-link tag="li" v-show="active==0||active==1" to="/MineOrderDetails/type/1" class="order-items bottom-space">
				<div class="order-top">
					<span class="order-num">210480431068</span>
					<span class="order-status">等待付款</span>
				</div>
				<div class="order-center">
					<div class="order-icon"></div>
					<div class="order-details">
						<p class="order-title">席子</p>
						<div class="order-style">
							<p>颜色:白色，尺码:L，类型:经典款</p>
							<p>x1</p>
						</div>
						<div class="order-price">￥99</div>
					</div>
				</div>
				<div class="after-sales-status" v-if="show">退货退款 退款成功</div>
				<div class="order-btn">
					<div class="btn-left" data-type="cancel" @click.stop="orderBtnFn($event)">取消订单</div>
					<div class="btn-right">付款</div>
				</div>
			</router-link>
			<router-link tag="li" v-show="active==0||active==2" to="/MineOrderDetails/type/2" class="order-items bottom-space">
				<div class="order-top">
					<span class="order-num">210480431068</span>
					<span class="order-status">去扫码取衣</span>
				</div>
				<div class="order-center">
					<div class="order-icon"></div>
					<div class="order-details">
						<p class="order-title">席子</p>
						<div class="order-style">
							<p>颜色:白色，尺码:L，类型:经典款</p>
							<p>x1</p>
						</div>
						<div class="order-price">￥99</div>
					</div>
				</div>
				<div class="after-sales-status" v-if="show">退货退款 退款成功</div>
				<div class="order-btn">
					<div class="btn-left" data-type="refund" @click.stop="orderBtnFn($event)">退款</div>
					<div class="btn-right">扫码取衣</div>
				</div>
			</router-link>
			<router-link tag="li" v-show="active==0||active==3" to="/MineOrderDetails/type/3" class="order-items bottom-space">
				<div class="order-top">
					<span class="order-num">210480431068</span>
					<span class="order-status">取货成功</span>
				</div>
				<div class="order-center">
					<div class="order-icon"></div>
					<div class="order-details">
						<p class="order-title">席子</p>
						<div class="order-style">
							<p>颜色:白色，尺码:L，类型:经典款</p>
							<p>x1</p>
						</div>
						<div class="order-price">￥99</div>
					</div>
				</div>
				<div class="after-sales-status" v-if="show">退货退款 退款成功</div>
				<div class="order-btn">
					<div class="btn-left" data-type="afterSales" @click.stop="orderBtnFn($event)">申请售后</div>
					<div class="btn-right" v-if="show"></div>
				</div>
			</router-link>
			<!-- <router-link tag="li" to="/MineOrderDetails/type/0" class="order-items bottom-space close-order-items">
				<div class="order-top">
					<span class="order-num">210480431068</span>
					<span class="order-status">交易关闭</span>
				</div>
				<div class="order-center">
					<div class="order-icon"></div>
					<div class="order-details">
						<p class="order-title">席子</p>
						<div class="order-style">
							<p>颜色:白色，尺码:L，类型:经典款</p>
							<p>x1</p>
						</div>
						<div class="order-price">￥99</div>
					</div>
				</div>
				<div class="after-sales-status" v-if="show">退货退款 退款成功</div>
				<div class="order-btn">
					<div class="btn-left" data-type="del" @click.native="orderBtnFn($event)">删除订单</div>
					<div class="btn-right" v-if="show"></div>
				</div>
			</router-link> -->
		</ul>
	</div>
</template>

<script>
export default {
	name: 'MineOrder',
	data () {
		return {
			show:false,
			active:0,
			tabList:["全部","待付款","待取货","已取货","售后"]
		}
	},
	created (){
		document.title="我的订单";
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		tabFn:function(index) {
			console.log(index)
			this.active=index;
		},
		orderBtnFn:function(e){
			switch(e.target.getAttribute("data-type")){
				// case "del":
				// 	CF.showConfirmTips("您确定删除此订单",function(){
				// 		CF.showTips("订单删除成功");
				// 	})
				// 	break;
				case "refund":
					CF.showConfirmTips("您确定要申请退款",function(){
						CF.showTips("订单删除成功");
					},"再想想","确定")
					break;
				case "cancel":
					CF.showConfirmTips("您确定要取消该订单",function(){
						CF.showTips("取消订单成功");
					})
					break;
				case "afterSales":
					this.$router.push({ path: '/AfterSales' })
					break;
				case "check":
					this.$router.push({ path: '/RefundDetails' })
					break;
			}
		}
	}
}
</script>

<style scoped>
	body{
		background-color: #e5e5e5;
	}
	div{
		background-color: #fff;
	}
	.tab-bar{
		display: flex;
		display: -webkit-flex;
		border: 1px solid #e5e5e5;
		padding-bottom: 0.5rem;
	}
	.tab-bar li{
		width: 20%;
		text-align: center;
		font-size: 0.875rem;
		color: #666;
		padding: 0.5rem 0;
		margin: 0 1rem;
		box-sizing: border-box;
	}
	.tab-bar li.cur{
		color: #000;
		border-bottom: 2px solid #000;
	}
	.order-items{
		font-size: 0.875rem;
	}
	.order-items>div{
		margin: 0 0.8rem;
		padding: 0.5rem 0;
	}
	.order-top{
		border-bottom: 1px solid #e5e5e5;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.order-center{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		border-bottom: 1px solid #e5e5e5;
	}
	.order-icon{
		width: 5rem;
		height: 5rem;
		background: url("../assets/clothes-bg.png") center left no-repeat;background-size: 80% auto;
		flex-shrink:0;
		-webkit-flex-shrink:0;
	}
	.order-details{
		width: 100%;
	}
	.order-style{
		display: flex;
		display: flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.order-price{
		margin-top: 0.8rem;
	}
	.order-items .after-sales-status{
		border-bottom: 1px solid #e5e5e5;
		padding-left: 1.5rem;
		background: url("../assets/ico_tui.png") center left no-repeat;background-size: auto 50%;
	}
	.order-btn{
		display: flex;
		display: -webkit-flex;
		justify-content: flex-end;
		-webkit-justify-content: flex-end;
	}
	.order-btn>div{
		width: 4.8rem;
		border: 1px solid #000;
		border-radius: 0.25rem;
		text-align: center;
		line-height: 1.5rem;
	}
	.order-btn .btn-right{
		background-color: #000;
		color: #fff;
		margin-left: 0.5rem;
	}
	.order-num{
		color:#666;
	}
	.order-style{
		color: #999;
	}
</style>



