<template>
	<div class="register">
		<ul class="register-list">
			<li class="register-items">
				<input ref="phone_num" type="tel" placeholder="输入手机号码" :value="phone | num" @keyup="phoneChange" maxlength="11">
			</li>
			<li class="register-items register-code">
				<div class="register-items-left">
					<input id="code" class="code-input" type="text" maxlength="4" placeholder="输入验证码">
				</div>
				<div class="register-items-right">
					<div class="code">
						<img src="../assets/code.png">
					</div>
					<div class="refresh" @click="refreshBol=!refreshBol"><img ref="refresh_img" :class="refreshBol?'turn':''" src="../assets/ico_refresh.png"></div>
				</div>
			</li>
		</ul>
		<div class="submit-button" ref="submit_btn" @click="submitFn(pageType)">确认</div>
	</div>
</template>

<script>
export default {
	name: 'Register',
	filters: {
		"num": function(value) {
			return value.replace(/[^\d]/g, '')
		}
	},
	data () {
		return {
			phone:"",
			refreshBol:false,
			pageType:this.$route.params.type
		}
	},
	created (){
		console.log(this.$route.params.type)
		if (this.pageType=="register") {
			document.title="注册";
		}else{
			document.title="忘记密码";
		}
		window.scrollTo(0, 0);
	},
	mounted () {

	},
	methods: {
		phoneChange(e) {
			this.phone = e.target.value
			this.$forceUpdate()  // 这里必须有
		},
		submitFn: function(t){
			var telephone = this.$refs['phone_num'].value;
			if (telephone.length<11) {
				CF.showTips("手机号码格式错误");
				return;
			}
			if (t=="register") {
				axios.post("/api/sms/send",{"type":1,"telephone":telephone}).then(res=>{
					if (res) {
						console.log(res)
						if (res.data.code==1001) {
							this.$router.push({ path: '/SettingPassword/type/register/tel/'+telephone })
						}else{
							CF.showTips(res.data.msg)
						}
					}else{

					}
				}).catch(function (error) {
					CF.showTips("验证失败")
				});
			}else{
				axios.post("/api/sms/send",{"type":2,"telephone":telephone}).then(res=>{
					if (res) {
						console.log(res)
						if (res.data.code==1001) {
							this.$router.push({ path: '/SettingPassword/type/setting/tel/'+telephone })
						}else{
							CF.showTips(res.data.msg)
						}
					}else{

					}
				}).catch(function (error) {
					CF.showTips("验证失败")
				});
			}
		}
	}
}
</script>

<style scoped>
	.register{
		padding: 1rem;
	}
	.register-list{
		margin-top: 4rem;
	}
	.register-items{
		padding: 1rem 0;
		border-bottom: 1px solid #e5e5e5;
	}
	.register-code,.register-items-right{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		align-items: center;
		-webkit-align-items: center;
	}
	.register-items input{
		width: 100%;
		font-size: 1rem;
	}
	.register-items-left{
		width: 90%;
	}
	.register-items-right{
		color: #999;
	}
	.refresh{
		width: 1.2rem;
		padding: 0.5rem;
	}
	.refresh img.turn{
		-webkit-animation:page-anim-loading 1s infinite linear;
		animation: page-anim-loading 1s infinite linear;
	}
	@keyframes page-anim-loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg);}}
</style>



